
import F2 from '@antv/f2';
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

export default {
props: ['main', 'money', 'status'],
components: {
Swiper,
SwiperItem,
Grid,
GridItem
},
mounted: function() {
this.renderChart();
this.renderChart2();
},
data() {
return {
moneyTypeHeader: ['FBA', '在途', '头程', '中转仓', '海外仓'],
timeDimensionIndex: 0,
index: 0,
chart: null,
chart2: null
};
},
computed: {
height: function() {
return window.innerWidth > window.innerHeight
? window.innerHeight - 54
: window.innerWidth * 0.707 + ''
},
chartData: function() {
var res = [];
switch (this.money) {
case 0:
res[0] = {name: '其他库存', percent: this.main.totalMoney - this.main.fbaMoney, a: 1};
res[1] = {name: '可售库存', percent: this.main.fbaMoney, a: 1};
break;
case 1:
res[0] = {name: '其他库存', percent: this.main.totalMoney - this.main.overseaTransferMoney, a: 1};
res[1] = {name: '海外在途', percent: this.main.overseaTransferMoney, a: 1};
break;
case 2:
res[0] = {name: '其他库存', percent: this.main.totalMoney - this.main.headMoney, a: 1};
res[1] = {name: '头程在途', percent: this.main.headMoney, a: 1};
break;
case 3:
res[0] = {name: '其他库存', percent: this.main.totalMoney - this.main.transferWarehouseMoney, a: 1};
res[1] = {name: '中转仓库存', percent: this.main.transferWarehouseMoney, a: 1};
break;
default:
res[0] = {name: '其他库存', percent: this.main.totalMoney - this.main.overseaStockMoney, a: 1};
res[1] = {name: '海外仓', percent: this.main.overseaStockMoney, a: 1};
break;
}
return res;
},
chartDataPCS: function() {
var res = [];
switch (this.money) {
case 0:
res[0] = {name: '其他库存', percent: this.main.totalPcs - this.main.fbaPcs, a: 1};
res[1] = {name: '可售库存', percent: this.main.fbaPcs, a: 1};
break;
case 1:
res[0] = {name: '其他库存', percent: this.main.totalPcs - this.main.overseaTransferPcs, a: 1};
res[1] = {name: '海外在途', percent: this.main.overseaTransferPcs, a: 1};
break;
case 2:
res[0] = {name: '其他库存', percent: this.main.totalPcs - this.main.headPcs, a: 1};
res[1] = {name: '头程在途', percent: this.main.headPcs, a: 1};
break;
case 3:
res[0] = {name: '其他库存', percent: this.main.totalPcs - this.main.transferWarehousePcs, a: 1};
res[1] = {name: '中转仓库存', percent: this.main.transferWarehousePcs, a: 1};
break;
default:
res[0] = {name: '其他库存', percent: this.main.totalPcs - this.main.overseaStockPcs, a: 1};
res[1] = {name: '海外仓', percent: this.main.overseaStockPcs, a: 1};
break;
}
return res;
}
},
watch: {
status: function() {
if(this.status % 2 === 0) {
this.chart.clear();
this.chart2.clear();
this.renderChart();
this.renderChart2();
}
}
},
methods: {
renderChart: function() {
let _this = this;
const chart = new F2.Chart({
width: window.innerWidth,
height: _this.height,
id: 'myChart1',
pixelRatio: window.devicePixelRatio 
});

chart.source(_this.chartData, {
percent: {
formatter(val) {
return val + '元';
}
}
});
chart.tooltip(false);
chart.legend({
position: 'right',
nameStyle: {
opacity: 0.4,
fill: '#fff', // 文本的颜色
fontSize: 16 
},
itemFormatter(val) {
return val;
}
});


chart.coord('polar', {


transposed: true, // 是否进行转置
innerRadius: 0.8, // 空心圆的半径大小
radius: 0.9 
});
chart.axis(false);
chart
.interval()
.position('a*percent')
.color('name', ['#324F77', '#857658'])
.adjust('stack')
.size(5);
chart.guide().html({
position: ['50%', '45%'],
html: `<div style="width: 200px;height: 40px;text-align: center;">
<div style="font-size: 12px;color:#FF9F26;">占比：${Number(_this.chartData[1].percent / (_this.chartData[0].percent + _this.chartData[1].percent) * 100).toFixed(2) + '%'}</div>
<div style="font-size: 18px;color:#fff;">${_this.$toThousands(_this.chartData[1].percent)}</div>
<div style="font-size: 12px;color:#ddd;">${_this.moneyTypeHeader[_this.money]}库存（元）</div>
</div>`
});

chart.render();
this.chart = chart;
},
renderChart2: function() {
let _this = this;
const chart = new F2.Chart({
width: window.innerWidth,
height: _this.height,
id: 'myChart2',
pixelRatio: window.devicePixelRatio 
});

chart.source(_this.chartDataPCS, {
percent: {
formatter(val) {
return val + '元';
}
}
});
chart.tooltip(false);
chart.legend({
position: 'right',
nameStyle: {
opacity: 0.4,
fill: '#fff', // 文本的颜色
fontSize: 16 
},
itemFormatter(val) {
return val;
}
});


chart.coord('polar', {


transposed: true, // 是否进行转置
innerRadius: 0.8, // 空心圆的半径大小
radius: 0.9 
});
chart.axis(false);
chart
.interval()
.position('a*percent')
.color('name', ['#324F77', '#857658'])
.adjust('stack')
.size(5);
chart.guide().html({
position: ['50%', '45%'],
html: `<div style="width: 200px;height: 40px;text-align: center;">
<div style="font-size: 12px;color:#FF9F26;">占比：${Number(_this.chartDataPCS[1].percent / (this.chartDataPCS[0].percent + _this.chartDataPCS[1].percent) * 100).toFixed(2) + '%'}</div>
<div style="font-size: 18px;color:#fff;">${_this.$toThousands(_this.chartDataPCS[1].percent)}</div>
<div style="font-size: 12px;color:#ddd;">${_this.moneyTypeHeader[_this.money]}库存PSC（件）</div>
</div>`
});

chart.render();
this.chart2 = chart;
}
}
};



